  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  border: 2px solid #dddddd;
  background: #ffffff;
  outline: none;
  transition: border-color 0.5s;
}

.login-form__input:focus {
  border-color: #e6bc98;
}

.login-form__input::-webkit-input-placeholder {
  color: #aaaaaa;
}

